.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  background-color: white;
}

.header-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-position: center 30%;
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.header-text {
  display: flex;
  font-family: serif;
  flex-direction: column;
  margin-top: 1rem;
}

.header-text h1 {
  margin: 0;
  font-size: 3.5rem;
}

.header-text p {
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  font-size: 2rem;
  color: #666;
}

.image-row {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.icon-row {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.about-me {
  background-color: #f8f8f8;
  width: 50%;
  margin: 2rem auto;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.about-me h2 {
  margin-top: 0;
  font-size: 1.5rem;
  color: #333;
}

.about-me p {
  font-size: 1rem;
  color: #666;
  line-height: 1.5;
}

.icon {
  width: 24px;
  height: 24px;
  margin: 0 8px;
}
